@import url(https://fonts.googleapis.com/css2?family=Domine:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Inconsolata);
.header{
    display: flex;
  overflow: hidden;
  background-image: url(/fbi-wanted-react/static/media/fbi-banner.815005ec.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  align-items: center;
  justify-content: space-evenly; 
box-shadow: 0px 10px 13px -7px #0084ff, 2px 3px 17px -30px rgba(0,0,0,0.19);
}

.header-fbi-container{
    display: flex;
    align-items: center;
    padding: 0.1rem;
}

.fbi-logo{
    height: 150px;
}

.FBI-title{
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    padding: 14px 16px;
    text-align: center;
    font-family: 'Domine', serif;
}

.FBI-title:hover{
    color: rgb(255, 255, 255);
}

.initials{
    font-size: 6rem;
    line-height: 1.3;
    text-shadow: 0 4px 3px rgb(0 0 0 / 40%), 0 8px 13px rgb(0 0 0 / 10%), 0 18px 23px rgb(0 0 0 / 10%);
}

.fullname{
    font-size: 2.3rem;
    width: 11em;
    text-transform: uppercase;
    display: inline-block;
    letter-spacing: 1px;
    padding-left: 7px;
    text-shadow: 0 4px 3px rgb(0 0 0 / 40%), 0 8px 13px rgb(0 0 0 / 10%), 0 18px 23px rgb(0 0 0 / 10%);
}
.Navbar{
    margin-top: 1px;
    text-align: center;
    box-shadow: 1px 5px 5px 1px rgba(7, 50, 129, 0.555);
}

.filters-title{
    text-align: center;
    font-size: 35px;
    font-weight: bolder;
    letter-spacing: 1px;
    text-shadow: 0px 1px 2px rgb(37, 97, 194);
    color: rgb(0, 0, 0);
}

.navbar-text{
    padding: 1rem;
    cursor:  pointer;
    font-size: 20px;
    margin: 10px;
}

.navbar-expand .navbar-nav .nav-link {
   color: black;
   font-size: 20px;
   margin: 10px;
}

.male-option, .female-option{
    display: block;
    width: 100%;
    padding: 0.25rem 1rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    text-decoration: none;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
    cursor: pointer;
}

.navbar-text a{
    text-decoration: none;
    font-weight: 400;
}

.navbar-text:hover{
    background-color: rgba(173, 173, 173, 0.52);
}

.navbar-expand .navbar-nav .dropdown-menu {
    position: absolute;
    font-size: 20px;
}
.wantedLayout-Container{
    display: flex;
    justify-content: center;
}

.mostWanted-Container{
    display: grid;
    grid-template-columns: repeat(3,10fr);
    grid-gap: 50px;
    gap: 50px;
    justify-content: center;
    padding: 5rem;
    overflow: scroll;
    overflow-y: hidden;
    overflow-x: hidden;
}

.criminaInfo-Container{
    padding: 1rem;
    text-align: left;
}
.most-wanted{
    height: -webkit-fill-available;
    border: 1px solid rgb(0, 0, 0);
    background-color: rgb(255 255 255 / 50%);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: -1px 2px 5px 2px rgb(255 255 255 / 31%);
}

.imgContainer{
    text-align: center;
    padding-top: 0.5rem;
}

.imgContainer img{
    border: 2px solid black;
}

.most-wanted p{
   color: rgb(0, 0, 0);   
   font-family: 'Domine', serif;
}

.Dis-Likes{
    display: flex;
    flex-direction: row;
    background-color: rgb(255, 255, 255);
    justify-content: space-around;
    border-top: 1px solid black;    
    cursor: pointer;
}

.Dis-Likes img {    
    height: 35px;
    width: 35px;
    padding: 5px;
}
.like-false{
    width: 100%;
    border: 0.5px solid black;
}

.dislike-false{
    width: 100%;
    border: 0.5px solid black;
}

.like-false:hover{
    background-color: rgba(0, 255, 21, 0.863);
}

.dislike-false:hover{
    background-color: rgba(255, 66, 66, 0.808);
}

.dislike-style{
    width: 100%;
    background-color: rgba(255, 66, 66, 0.808);
}

.like-style{
    width: 100%;
    background-color: rgba(0, 255, 21, 0.863);
} 
.buttons-Container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 1rem;
}

.buttons-Container button {
  background: linear-gradient(to bottom, #ffffff 5%, #f6f6f6 100%);
  background-color: #ffffff;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
  font-weight: "bolder";
  font-family: Arial;
  font-size: 17px;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  padding: 6px 24px;
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  cursor: pointer;
  color: #000000;
}

.buttons-Container button:hover {
  background: linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
  background-color: #656c8d;
  text-decoration: underline;
  text-shadow: none;
}

.backBtn-displayNone {
  display: none;
}
