.ErrorContainer{
margin-top: 350px;
height: 100%;
display: flex;
justify-content: center;
align-items: center;
text-align: center;
text-shadow: 0 4px 3px rgb(0 0 0 / 40%), 0 8px 13px rgb(0 0 0 / 10%), 0 18px 23px rgb(0 0 0 / 10%);
}

.titleContainer h2{
    font-size: 80px;
    color: #0089ff;
}

.backLink{
    font-size: 2rem;
    font-weight: bolder;
    word-spacing: 1px;
    letter-spacing: 1px;
}


* {
  box-sizing: border-box;
}

html, body {
  height: auto;
  margin: 0;
  padding: 0;
}

body {
  display: block;
  background-image: url(/fbi-wanted-react/static/media/fbi-backgroundImg.3189b98a.png);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px);
  background-position: center;
  background-size: auto;
  background-repeat: repeat;
}

#root {
  height: 100%;
}

.results-container {
  text-align: center;
}

.spinner {
  width: 150px;
  height: 150px;
  font-size: xx-large;
  margin: 10rem 0;
}

@media screen and (max-width: 850px) {
  html, body {
    min-width: 300px;
  }
  .fbi-logo {
    height: 90px;
    width: 90px;
  }
  .fullname {
    font-size: 1rem;
  }
  .initials {
    display: none;
  }
  .navbar > .container {
    display: block;
  }
  .filters-title {
    display: none;
  }
  .navbar {
    height: 90px;
  }
  .container {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
  }
  .navbar-text, #basic-nav-dropdown {
    font-size: 15px;
  }
  .navbar-collapse {
    flex-direction: row;
  }
  .navbar-brand {
    display: none;
  }
  .navbar-nav {
    height: 45px;
  }
  .me-auto {
    margin-right: 0;
  }
  .mostWanted-Container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem;
  }
  .most-wanted {
    height: auto;
  }
}
/*# sourceMappingURL=index.css.map */
